/*!
 * Theming
 */


/* BODY */

body {
    font-family: 'Rubik', sans-serif;
    color: #000000;
    font-weight: 300;
    font-size: 16px;
}

.row {
    min-height: 35px;
}

.text-primary {
    color: #008CFF !important;
}


/* NAV*/

.navbar {
    padding: 2rem 1rem 0.5rem;
}


.logo {
    width: 100px;
    height: auto;
}

.bg-light {
    background-color: #fff !important;
}

.nav-item {
    padding: 0.5rem;
}

.nav-link {
    padding: 0.2rem;
}


/* bootstrap components complements */

.alert-title,
.alert a:not(.btn) {
    font-size: 14px;
    color: #008CFF;
    text-decoration: none;
}

.alert p {
    font-weight: 300;
    margin-bottom: 0px;
}

.alert-footer {
    margin-bottom: 0px;
}

.alert-footer:before {
    content: "";
    height: 2px;
    width: calc(100% + 132px);
    background-color: #FFFFFF;
    display: block;
    position: relative;
    top: -15px;
    left: -25px;
}

.form-control-2+.input-group-addon {
    position: absolute;
    right: 0;
    z-index: 99;
}

.icon-btn {
    padding: 4px 8px!important;
    margin-left: 5px;
    border-radius: 5px;
}

.icon-btn:hover .stroke-transparent {
    -webkit-text-stroke: 1px #008CFF;
}

.fa-file-alt {
    color: white;
    font-size: 1.5em;
